"use strict";
var NavService = (function () {
    function NavService() {
        this.navItems = new Array();
    }
    NavService.prototype.addNavItem = function (navItem) {
        this.navItems.push(navItem);
    };
    return NavService;
}());
exports.NavService = NavService;



